body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* Ecom */

/* Navbar */

.main-login{
  margin: 40px auto;
  width: 600px;
}
.align-left ul{
  margin: 0 !important;
  float: right !important;
}
.align-left ul li{
  float: right !important;
}

.main-signup{

  margin: 40px auto;
  width: 60%;
}
.products{
margin: 0 auto;
width: 90%;
min-width: 350px;
max-width: 90%;
}
.product-img{
  width: 90%;
  height: auto;
}
.pcard{
  margin: 10px auto ;
}
.c-img{
  height: 209px;
}
.cbody{
  padding: 8px; 
}
.cbody p{
  margin: 0;
}
.slide{
