.nav-ul li{
display: inline;

}
.product-type{
  width: 80%;
  margin: 0 auto;
  height: auto;
}
.product-cat-card{
  margin: 10px auto;

}
.product-type-link:hover{
text-decoration: none;
}

.ad-form{
  margin: 0 auto;
  width: 50%;
  margin: 2px solid black;
}
fieldset { 
  display: block;
  margin-left: 2px;
  margin-right: 2px;
  padding-top: 10px;
  padding-bottom: 0.625em;
  padding-left: 0.75em;
  padding-right: 0.75em;
  border: 2px groove;
}


/* ad-details */
.ad-details-main{
  
  width:98%;
  margin:  20px auto;
}
.ad-details-main div{
  padding:0;
}
.p-img{
  margin: 0 1%;
  width: 98%;
  height: auto;
  /* max-height: 500px; */
  border:1px solid gray;
  padding: 5px;
}
.p-details{
  margin: 10px 1%;
  width: 98%;
  border:1px solid gray;
  padding: 5px;
  overflow: hidden;
}
.p-details span{
  width: 50%;
  margin: 0 0;
  float: left;
  overflow: hidden;
}
.p-details span p{
  width: 50%;
  margin: 0 0;
  float: left;
  overflow: hidden;
} 
.p-img img{
  margin: 5px 2.5%;
  width: 95%;

}
.product-price{
  margin: 0 0 8px 0;
  width: 100%;
  border: 1px solid gray;
}
.seller-desc{
  margin: 0 0 8px 0;
  /* font-size: 20px; */
  width: 100%;
  border: 1px solid gray;  
}
.d-profile-pic img{
  border-radius: 50px;
  width: 60px;
  margin: 10px 0 10px 10px;  

}
.profile-name{
  display: inline-block;
  margin: 0 5px;
}
.d-telephone{
  display:block;
  width:90%;
  padding: 10px 0;
  margin:10px auto;
  text-align: center;
  color: white;
 }
 .d-telephone:hover{
  text-decoration:none;
  display:block;
  width:90%;
  padding: 10px 0;
  margin:10px auto;
  text-align: center;
  color: white;
 }
.seller-desc h4{
  font-size: 20px;
  font-weight: bolder;
  margin: 5px 10px;
}
.product-price h4{
  font-size: 30px;
  font-weight: bolder;
  margin: 5px 10px;
}
.product-title {
  color: gray;
  font-size: 18px;
  font-weight: 500;
  width: 90%;
  overflow: hidden;
  margin: 5px 10px;
}

.product-price span p{
display: inline-block;

}
.P-Location{
  color: gray;

  width: 180px;
  margin: 10px 0 10px 10px;  
  float: left;

}
.ad-date{
  color: gray;

  margin: 10px 0 10px 10px;
  font-size: 15px;
}
.msg-posted{
  position: absolute;
    top: 70%;
    left: 55%;
    width: 300px;
    height: 200px;
    margin-top: -150px;
    margin-left: -200px;
    border:2px solid gray;
    border-radius:10px; 
    display: none;

}
.btn-123{
  margin: 37px 36%;
}
.Profile{
  margin: 20px auto;
  border: 1px solid gray;
}
.Profile-Content{
  width: 400px;
  margin: 10px auto; 
}
.profile-image{
  width: 150px;
  height: 150px;
  margin: 0 auto;
  

}
.profile-image img{
  border:1px solid gray;
  border-radius:50%;
}
.Profile-Content h5{
  text-align: center;
  margin: 15px auto;
}
.btn-div button{
  margin: 5px 40.124%;

}
.btn-div1 button{
  margin: 5px 37%;

}


.card-btn-div{
  padding: 0 auto;
}
.btn1{
  margin: 2% 14%;
}
.nbar{
  width:100%;
  margin:0;
}

.pedit label{
margin: 0 10px;  
}



.main-profile-update{

  margin: 40px auto;
  width: 50%;
  border: 1px solid gray; 
  padding: 15px;
  border-radius: 10px;
}
.main-profile-update-container{
  /* border: 1px solid gray; */
}
/* Profile Page */

.captions{
  bottom: 99px;

}
.captions h1{
  /* color:teal; */
  font-size: 60pt;
  font-weight: 900;
  bottom: 40px;
}
.captions p{
  /* color:teal;Profile */
  font-size: 30pt;
  font-weight: 900;
  bottom: 40px;
}



/* Media Queries */
/* Extar small */
@media (min-width: 350px ) { 
  .product-form{
    width: 80%;
    margin: 40px auto;
  }
  .main-profile-update{

    margin: 40px auto;
    width: 70%;
  }
}
/* Small */

@media (min-width: 576px ) { 
.product-form{
  width: 60%;
  margin: 40px auto;
}
 }
/* medium */
@media (min-width: 768px) { 
  .product-form{
    width: 50%;
    margin: 40px auto;
  }
 }
@media (min-width: 992px) { 
  .nav-ul{
    margin: 0 0 0 80px;

  }
  /* sign up */
  .product-form
  .main-signup{

    margin: 40px auto;
    width: 45%;
  }
  .main-profile-update{

    margin: 40px auto;
    width: 45%;
  }

}
@media (min-width: 1200px) { 
  .nav-ul{
    margin: 0 0 0 170px;
  }
  /* Post Product Form */
  .product-form{
    width: 40%;
    margin: 40px auto;
  }

  .main-signup {

    margin: 40px auto;
    width: 45%;
  }
}
